//main container modes
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,900|Encode+Sans:400,700|Libre+Baskerville');


//local Variables
$accent-gray: #a2b1ba;
$accent-aqua: #0cf;
$accent-rose: #774B4D;
$accent-maroon: #9E4D4A;
$accent-blue: #B39FA3;

$theme-white: white;
$theme-black: #1B2025;

$heading-font: 'Montserrat', sans-serif;
$body-font: 'Encode Sans', sans-serif;
$fw-bold: 600;
$fw-black: 900;

$border-radius-md: .6rem;
$border-radius-lg: 4rem;

::root {
  font-size: 100%; }

html {}

body {
  line-height: 1.6;
  font-family: $body-font;
  color: $theme-black; }
h1,
h2,
h3,
h4,
h5 {
  font-weight: normal;
  margin: 0 0 .8rem;
  padding: 0;
  line-height: 1.6;
  font-family: $heading-font;
 }  // font-weight: $fw-bold

h1 {
  font-size: 4.6rem;
  color: $theme-black; }
h2 {
  font-size: 3.2rem; }
h3 {
  font-size: 2.8rem; }
h4 {
  font-size: 1.4rem; }
h5 {
  font-size: 1.0rem; }

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child {
  margin-top: 0;
  padding-top: 0;
 }  // line-height: 3.6rem

p {
  margin: 0 0 1rem;
  letter-spacing: .4px;
  font-size: 1rem;
  line-height: 1.8;

  &.headline {} }

* p:only-child,
* p:last-child {
  margin-bottom: 0; }

a {
  text-decoration: none;
  color: $accent-gray;
  transition: .2s;

  &:active,
  &:visited {
    text-decoration: none; }

  &:hover {
    text-decoration: none;
    color: $accent-aqua; }


  small {
    font-size: .8rem; } }


strong {
  font-weight: 600; }

.free {
  min-height: 16rem;
  background: $theme-white;
  overflow: hidden;
  text-align: left; }

.full {
  width: 100vw;
  height: 100vh; }

.confined {
  max-width: 1440px;
  margin: 0 auto;
 }  // background: $theme-white

.section-header {
  margin-top: 6rem;
  text-align: center; }

.g-heading {
  max-width: 85vw; }

.flexible-content {
  display: flex;
  flex-flow: row wrap;
  align-items: stretch;

  @include bp(large-screen) {
    height: 100vh; }

  @include bp(big-screen) {
    height: 100vh; }


  & > section {
    position: relative; } }


.title-bar {
  display: grid;
  justify-items: center;
  align-items: center;
  position: relative;

  h1 {
    letter-spacing: .1rem; }
  &.g1 {
    background-image: linear-gradient(0deg, #0cf 0%, #dc32bb 96%); }
  &.g2 {
    //background-image: linear-gradient(to right, #ed6ea0 0%, #ec8c69 100%)
    background-image: linear-gradient(to bottom, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ec8c69 100%); }
  svg {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%); } }

.topbar {
  display: flex;
  flex-flow: row wrap;
  font-family: $heading-font;
  background: rgba($theme-black, 1);
  margin: 0 0 2rem;
  padding: .6rem 1rem;
  width: 100%;
  position: fixed;
  z-index: 10000;

  .branding {
    flex: 0 1 50%; }

  .controls {
    flex: 0 1 50%;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end; }

  .contact {}

  .logo {
    font-size: 1.0rem;
    transform-origin: left top;

    & > * {
      display: inline-block;
      white-space: nowrap;
      transition: .4s; }

    span {
      color: rgba($theme-white,.24); }

    .pip {
      width: 0;
      opacity: 0; }

    .sulp {
      margin-right: 5px; }


    .ex {
      width: 20px;
      transition: .4s .3s; }

    .ico {
      width: 0;
      opacity: 0; }

    span {
      color: $theme-white; }

    &:hover {
      span {
        color: rgba($theme-white, 1); } } }


  .logo:not(.expanded):hover,
  .logo.expanded {

    transition: .4s 0;

    & > * {}

    .pip {
      width: 60px;
      opacity: 1;
      transition: .6s; }

    .ex {
      width: 0;
      opacity: 0;
      transition: .6s; }

    .ico {
      width: 62px;
      opacity: 1;
      transition: .6s .2s; } }



  h2 {
    font-size: 1.4rem;
    margin-bottom: 0rem;
    line-height: 1.5; }

  &__tagline {
    font-size: .8rem;
    letter-spacing: .1rem;
    color: $accent-gray; } }


// SLIDESHOW

.home {}

.slideshow {
  z-index: 5000;
  height: 100vh;
  width: 100%;

  section {
    height: 100vh;
    width: 100%;
    overflow: hidden;
    position: relative;
    background: $theme-black;
    transition: 1s;

    &.blar {
      background-color: $theme-black;
      transition: 1.5s; }

    article {
      overflow: hidden;
      background: black;
      backface-visibility: hidden;
      -webkit-backface-visibility: hidden;
      z-index: 1000;
      height: 100vh;
      width: 100%;
      margin-left: 50%;
      margin-top: calc(100vh / 2);
      transform-origin: 50% 50%;
      transform: translateX(-50%) translateY(-50%) scale(1) perspective(1400px) rotateX(0) rotateY(0);
      filter: blur(0);
      opacity: 1;
      -webkit-filter: blur(0);
      animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
      transition: all 1.6s;

      &.next-form {
        transform: translateX(-50%) translateY(-50%) scale(.8) perspective(1200px) rotateX(15deg) rotateY(0);
        filter: blur(25px);
        opacity: .1;
        -webkit-filter: blur(25px);
        animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
        transition: all 2s; }

      &.prev-form {
        transform: translateX(-50%) translateY(-50%) scale(.8) perspective(1200px) rotateX(-15deg) rotateY(0);
        filter: blur(25px);
        opacity: .1;
        -webkit-filter: blur(25px);
        animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
        transition: all 2s; } } } }

#pager {
  z-index: 9000;
  position: fixed;
  right: 0;
  top: 50%;
  padding: 10px 20px;
  transform: translateY(-50%);
  color: $theme-white;

  a {
    display: block;
    background: $theme-white;
    height: 4px;
    width: 4px;
    font-size: 0;
    text-indent: -10rem;
    overflow: hidden;
    border-radius: 2px;
    margin: 1rem 0;
    opacity: .2;

    &.selected {
      opacity: 1;
      height: 16px; } } }


