// FLICKERING LIGHTS


.flicker {
  animation: dim 2.6s ease infinite; }

@keyframes dim {
  0% {
    opacity: .0; }

  1% {
    opacity: 1; }

  2% {
    opacity: .4; }

  3% {
    opacity: 1; }

  60% {
    opacity: 1; }

  62% {
    opacity: .2; }

  63% {
    opacity: .8; }

  64% {
    opacity: .3; }

  67% {
    opacity: 1; }

  9% {
    opacity: 1; } }


// BLINKING CURSOR
.blink {
  animation: blinker steps(1,end) 1s infinite; }

@keyframes blinker {
  0% {
    opacity: 0; }

  50% {
    opacity: 1; } }

