  margin: 0; }

.bg-red {
  background-color: red; }

.color-red {
  color: red; }


//  HERO

.hero {
  min-height: 40rem;
  background: $theme-black;

  h1, h2, h3, h4, h5 {
    color: white; } }

.blog-hero {
  margin: 0 0 2rem;
  overflow: hidden;
  height: 400px;
  background: $theme-black;

  img {
    opacity: .8;
    width: 100vw;
    display: block; } }

.blog-title {
  margin: 0 0 2rem; }

.blog-content {
  display: flex;
  align-content: stretch;
  justify-content: center;
  margin: 0 0 6rem;
  flex-flow: row nowrap;

  @include bp(iphone5-landscape) {
    flex-flow: row wrap; }

  @include bp(ipad) {
    margin: 0 1.4rem; }

  .desc {
    padding: 1.2rem 1.6rem;
    flex: 0 1 25%;

    @include bp(large-screen) {
      border-top: 1px solid rgba($theme-black, .2);
      border-left: 1px solid rgba($theme-black, .2);
      border-bottom: 1px solid rgba($theme-black, .2);
      border-radius: .5rem 0 0 .5rem; }

    @include bp(iphone5-landscape) {
      background: red;
      border-radius: 0;
      flex: 0 0 100%; } }

  .images {
    background-color: rgba($accent-gray,.2);
    flex: 0 1 75%;

    @include bp(iphone5-landscape) {
      background: red;
      flex: 0 0 100%; }


    @include bp(large-screen) {
      border-radius: 0 .5rem .5rem 0;
      overflow: hidden; }

    img {
      max-width: 100%;
      display: block; } } }
