@mixin confine($width) {
  margin: 0 auto;
  width: #{$width}; }

@mixin flexxy($direction, $wrap, $align, $justify, $basis) {
  display: flex;
  flex-flow: $direction $wrap;
  align-items: $align;
  justify-content: $justify;

  & > * {
    flex: 1 1 $basis; } }

@mixin griddy {
  display: grid; }
